@charset "UTF-8";
html {
  height: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* calculator */
.calculator {
  height: 100%;
  display: grid;
  grid-template-rows: 2fr 1fr;
  grid-template-areas:
    "monitor"
    "price-selector";
}


/* inventory */
.inventory {
  height: 100%;
}

.inventory-contents {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(7, 1fr);
  grid-gap: 1rem;
}

.item-card{
  margin: 0;
  text-align :center;
  user-select: none;
  border-radius: 5%;
}

.item-card:active{
  background: yellowgreen;
  font-weight: bolder;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  transform: translateY(2px);
}

.item-card > * {
  display: block;
}

.item-id{
  display: none;
}

.item-name{
  font-size: 1.4rem;
  margin-top: 0.5rem;
}
.item-scale{
}
.item-price{
  margin-bottom: 1.2rem;
}

/* navBar */
.nav-bar{
  display: flex;
  justify-content: space-between;
  position: fixed;
  bottom: 0px;
  width: 100%;
  background: lightgray;
}

.nav-bar button{
  width: 200px;
  padding: 2rem;
}

.reservation-menu{
  background: bisque;
}

.calculator-menu{
  background: aquamarine;
}


/* submitter */
.submitter {
  display: flex;
  justify-content: space-between;
  background: lightgray;
}

.submitter button {
  width: 200px;
}

.submitter .cancel-button{
  background: bisque;
}

.submitter .reserve-button{
  background: powderblue;
}

.submitter .accept-button{
  background: aquamarine;
}



/* monitor */
.monitor {
  grid-area: monitor;
  position: relative;
}

.orderItem{
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(10, 1fr);
}

.orderRow{
  display: grid;
  grid-template-columns: 1fr 2fr 2fr 1fr;
  font-size: 1.4rem;
}

.totalPrice{
  position: absolute;
  font-size: 1.8rem;
  font-weight: bold;
  right: 0.5rem;
  bottom: 0.5rem;
}

.bold{
  font-weight: bolder;
}

.main-mark{
  text-align: center;
  font-size: 4rem;
  font-weight: bold;
}

.main-message{
  text-align: center;
  font-size: 1.5rem;
}

.message-row{
  margin: 0.8rem;
}

.status{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
  font-weight: bold;
  animation: fadeOut 0.8s 0s forwards;
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}


/* price-selector */
.price-selector{
  grid-area: price-selector
}

.price-selector-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

}

.price-card {
  text-align: center;
  margin: 0.5rem;
  padding: 0.7rem;
  width: 80%;
  border: 1px solid #515151;
}

.price-card:active{
  background: yellowgreen;
  font-weight: bolder;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  transform: translateY(2px);
}

.reservation {
  margin: 60px;
  position: fixed;
  background: white;
  border: solid;
  width: 80%;
  height: 80%;
  z-index: 1;
}

.user-list {
  margin: 2rem ;
}

.user-rows {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.customer {
  border: solid;
  border-radius: 1px;
  margin: 1rem;
}

.customer label {
  margin: 0px;
  width: 100%;
  height: 100%;
  display: block;
  background: wheat;
  padding: 1rem;
  box-sizing: border-box;
  /*appearance: none;*/
  /*-webkit-appearance: none;*/
  /*-moz-appearance: none;*/
}

.cancel {
  background: coral;
}

.reservation-row {
  display: grid;
  grid-template-columns: 0.5fr 0.7fr 0.5fr 1fr 0.5fr 1fr 1fr;
  border-top: solid;
}

.reservation-row span {
  box-sizing: border-box;
}

.order_column_id {
  font-size: 0.5rem;
  overflow: scroll;
}
.order_column_create_at{
}
.submit-controller {
  margin: 1rem;
}

.submit-buttons {
  display: grid;
  grid-template-columns: 1fr 2fr 2fr 2fr;
}

.submit-controller button{
  margin: 0;
  text-align :center;
  user-select: none;
  padding : 2rem;
  margin: 1rem;
}